.App {
  text-align: center;
  background-color: black;
  color: white;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
  overflow: hidden;
  position: relative;
  padding-top: 20px;
}

.shiva-container {
  margin-top: 20px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.shiva-image {
  width: 250px; /* Increased size */
  max-width: 90%;
  height: auto;
}

.buttons-container {
  margin-top: 20px;
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  gap: 10px;
}

button {
  background-color: #ff9800;
  color: white;
  font-size: 16px; /* Reduced size */
  padding: 8px 15px;
  border: none;
  border-radius: 8px;
  cursor: pointer;
  transition: 0.3s;
}

button:hover {
  background-color: #e68900;
}

.falling-word {
  position: absolute;
  font-size: 22px;
  animation: fall linear forwards;
}

@keyframes fall {
  0% {
    transform: translateY(-100vh);
  }
  100% {
    transform: translateY(100vh);
  }
}

.word-container {
  font-size: 22px;
  font-weight: bold;
  white-space: pre-line;
  margin-top: 20px;
}

.flower {
  position: absolute;
  width: 40px;
  animation: fall linear forwards;
}

/* Business Message */
.message-container {
  position: fixed;
  bottom: 20px;
  width: 90%;
  max-width: 600px;
  background-color: rgba(255, 255, 255, 0.1);
  border: 2px solid gold;
  padding: 15px;
  border-radius: 12px;
  text-align: center;
  color: white;
  font-size: 16px;
  animation: glow 1.5s infinite alternate;
}

@keyframes glow {
  0% {
    box-shadow: 0 0 10px gold;
  }
  100% {
    box-shadow: 0 0 20px white;
  }
}
